import React from "react";

import Layout from "../components/layout";
import SEO from "../components/seo";
import { withPrefix } from "gatsby";
import { isBrowser } from "../util/helper";

const NotFoundPage = () => {
	if (isBrowser()) {
		// eslint-disable-next-line no-console
		console.log(location.pathname);
		// eslint-disable-next-line no-console
		console.log(withPrefix(`/ops`));
	}
	const isClientRoute = isBrowser() && location.pathname.startsWith(withPrefix(`/ops`));
	if (isClientRoute) {
		return (
			<Layout>
				<SEO title="OPS" />
				<div className="container p-16">Betöltés...</div>
			</Layout>
		);
	} else {
		return (
			<Layout>
				<SEO title="404: Oldal nem található" />
				<div className="container p-16">
					<div className="lg:w-1/2 bg-brand-blue p-4 text-white shadow-xl m-auto text-center">
						<h1 className="text-6xl">404</h1>
						<h2 className="text-3xl">Sajnáljuk, ez az oldal nem elérhető!</h2>
					</div>
				</div>
			</Layout>
		);
	}
};

export default NotFoundPage;
